.section-profit-container {
  height: 554px;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
}
.section-profit-content {
  display: flex;
  align-items: flex-end;
}
.section-profit-content-items {
  width: 450px;
  height: 392px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.section-profit-content-item {
  display: flex;
  width: 405px;
  align-items: center;
  justify-content: flex-start;
}
.section-profit-content-item p {
  font-family: var(--font-inter);
  font-style: normal;
  font-weight: 300;
  font-size: 16px;
  line-height: 114%;
  letter-spacing: -0.02em;
  color: #1f252d;
  text-shadow: var(--text-shadow);
}
.section-profit-content-item > div {
  width: 64px;
  height: 64px;
  border: 1px solid rgba(2, 2, 2, 0.25);
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: 24px;
}

@media screen and (max-width: 767px) {
  .section-profit-container {
    margin-top: 20px;
    height: auto;
  }
  .section-profit-content {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
  }
  .section-profit-content-items {
    width: 100%;
    height: 392px;
    margin: 20px 0;
  }
  .section-profit-content-item {
    display: grid;
    width: 100%;
    grid-template-columns: auto auto;
  }
  .section-profit-content img {
    width: 100%;
  }
}
