.nav-item {
  font-family: var(--font-golos);
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 130%;
  color: var(--color-white);
}
.nav-item:hover {
  color: red;
}
.active-nav-item {
  color: red;
}

@media screen and (max-width: 767px) {
  .nav-item {
    font-weight: 500;
    font-size: 20px;
    line-height: 24px;
    text-transform: uppercase;
    color: #fff;
    text-underline-offset: 0.3em;
  }
  .nav-item:hover {
    text-decoration: none;
  }
  .nav-item-wrapper {
    position: relative;
    width: 100%;
    text-align: center;
  }
  .nav-item::before {
    content: "";
    width: 0rem;
    height: 0.1rem;
    background-color: #fff;
    position: absolute;
    top: 0.6rem;
    left: -4rem;
    transition: width 0.5s cubic-bezier(1, 0, 0, 1);
  }

  .nav-item::after {
    content: "";
    width: 0rem;
    height: 0.1rem;
    background-color: #fff;
    position: absolute;
    top: 0.6rem;
    right: -4rem;
    transition: width 0.5s cubic-bezier(1, 0, 0, 1);
  }
  .nav-item:hover::before,
  .nav-item:hover::after {
    width: 3rem;
  }
  .active-nav-item {
    text-decoration: none;
  }
  .active-nav-item::after {
    content: "";
    width: 3rem;
    height: 0.1rem;
    background-color: #fff;
    position: absolute;
    top: 0.6rem;
    right: -4rem;
  }
  .active-nav-item::before {
    content: "";
    width: 3rem;
    height: 0.1rem;
    background-color: #fff;
    position: absolute;
    top: 0.6rem;
    left: -4rem;
  }
}

#nav-icon {
  width: 33px;
  height: 23px;
  -webkit-transform: rotate(0deg);
  transform: rotate(0deg);
  transition: 0.5s ease-in-out;
  cursor: pointer;
  margin-bottom: 5px;
  /* margin-left: 10px; */
  display: none;
}
@media screen and (max-width: 767px) {
  #nav-icon {
    display: flex;
  }
}
#nav-icon.open {
  width: 33px;
  height: 23px;
}
#nav-icon span {
  display: block;
  position: absolute;
  height: 3px;
  width: 100%;
  background: var(--color-white);
  border-radius: 9px;
  opacity: 1;
  left: 0;
  -webkit-transform: rotate(0deg);
  transform: rotate(0deg);
  transition: 0.25s ease-in-out;
  z-index: 99;
}

#nav-icon span:nth-child(1) {
  top: 0px;
  width: 50%;
}

#nav-icon span:nth-child(2) {
  top: 8px;
}

#nav-icon span:nth-child(3) {
  top: 16px;
  width: 75%;
}
#nav-icon:hover span {
  width: 100%;
}
#nav-icon.open span:nth-child(1) {
  top: 8px;
  -webkit-transform: rotate(135deg);
  transform: rotate(135deg);
  width: 100%;
}

#nav-icon.open span:nth-child(2) {
  opacity: 0;
  left: -60px;
  width: 100%;
}

#nav-icon.open span:nth-child(3) {
  top: 8px;
  -webkit-transform: rotate(-135deg);
  transform: rotate(-135deg);
  width: 100%;
}

