.footer-container {
  width: 100%;
  height: 600px;
  background: #1e242c;
}
@media screen and (max-width: 767px) {
  .footer-container {
    width: 100%;
    height: auto;
    padding: 30px 0;
  }
}

.footer_top_content {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
}
@media screen and (max-width: 767px) {
  .footer_top_content {
    display: grid;
    grid-template-columns: 1fr;
    row-gap: 15px;
  }
}
.footer_link_items_wrapper {
  display: grid;
  grid-template-columns: 1fr;
  row-gap: 8px;
}
@media screen and (max-width: 767px) {
  .footer_link_items_wrapper {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: flex-start;
  }
}
.footer_link_item_title {
  font-family: var(--font-golos);
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 130%;
  color: #ffffff;
  margin-bottom: 12px;
}
.footer_link_items_wrapper .footer_link_item {
  font-family: var(--font-golos);
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 130%;
  color: #d2d3d5;
  text-shadow: var(--text-shadow);
}
@media screen and (max-width: 767px) {
  .footer_link_items_wrapper .footer_link_item {
    font-size: 12px;
    margin-right: 14px;
  }
}
.footer_link_item:hover {
  color: #ffffff;
}
.footer_top_bar_item {
  display: grid;
  grid-template-columns: 1fr;
}
.footer_line {
  display: inline-block;
  height: 1px;
  width: 100%;
  background: rgba(255, 255, 255, 0.05);
  margin: 84px 0 32px;
}
@media screen and (max-width: 767px) {
  .footer_line {
    margin: 24px 0;
  }
}
.footer_bottom_content {
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: relative;
}
@media screen and (max-width: 767px) {
  .footer_bottom_content img {
    position: absolute;
    top: 30px;
    right: 0;
    width: 100%;
  }
}

.footer_bottom_content-info {
  font-family: var(--font-golos);
  font-style: normal;
  color: #ffffff;
  line-height: 130%;
}
.footer_bottom_content-info-title {
  font-weight: 700;
  font-size: 16px;
  margin-bottom: 24px;
}
.footer_bottom_content-info-tel {
  font-weight: 400;
  font-size: 14px;
  opacity: 0.8;
}
.tel_2 {
  margin: 12px 0 18px;
}
.footer_bottom_content-subinfo {
  color: rgba(255, 255, 255, 0.25);
  font-weight: 400;
  font-size: 12px;
}
.footer_bottom_content-subinfo:nth-last-child(2) {
  margin: 10px 0 6px;
}
.footer_bottom_content-social-links {
  width: 214px;
  display: grid;
  grid-template-columns: repeat(5, 1fr);
}
.footer_bottom_content-social-link {
  width: 30px;
  height: 30px;
  border-radius: 50%;
  background-color: #fff;
  color: #1f252d;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 22px;
  cursor: pointer;
}
.footer_bottom_content-social-link:hover {
  box-shadow: 0 0 10px #fff;
}
