.argumentofhouse-container {
  height: 601px;
}
.argumentofhouse-wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 45px;
}
.argument-navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 234px;
  margin-bottom: 53px;
}
.argument-nav-item {
  width: 64px;
  cursor: pointer;
  font-family: var(--font-inter);
  font-style: normal;
  font-weight: 400;
  font-size: 12px;
  line-height: 130%;
  letter-spacing: -0.02em;
  color: rgba(2, 2, 2, 0.25);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.argument-nav-item p {
  text-shadow: var(--text-shadow);
}
.argument-nav-item-icon {
  width: 64px;
  height: 64px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  background: transparent;
  border: 1px solid rgba(2, 2, 2, 0.25);
  margin-bottom: 8px;
}
.argument-nav-item:hover .argument-nav-item-icon {
  background: #25252c;
  box-shadow: -6px 62px 25px rgba(0, 0, 0, 0.01),
    -3px 35px 21px rgba(0, 0, 0, 0.05), -1px 16px 16px rgba(0, 0, 0, 0.09),
    0px 4px 9px rgba(0, 0, 0, 0.1), 0px 0px 0px rgba(0, 0, 0, 0.1),
    inset 0px 4px 28px rgba(0, 0, 0, 0.1);
  transition: all 0.2s;
}
.argument-nav-item:hover p {
  color: #020202;
  transition: all 0.2s;
}

.active_argument_bg {
  background: #25252c;
  box-shadow: -6px 62px 25px rgba(0, 0, 0, 0.01),
    -3px 35px 21px rgba(0, 0, 0, 0.05), -1px 16px 16px rgba(0, 0, 0, 0.09),
    0px 4px 9px rgba(0, 0, 0, 0.1), 0px 0px 0px rgba(0, 0, 0, 0.1),
    inset 0px 4px 28px rgba(0, 0, 0, 0.1);
}
.active_argument_color {
  color: #020202;
}

.argument-content-wrapper {
  width: 1004px;
  height: 351px;
  display: flex;
  position: relative;
  overflow: hidden;
}
.argument-content-item {
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  width: 1004px;
}
.argument-content-info {
  width: 455px;
  margin-right: 74px;
}
.argument-content-info p:first-child {
  font-family: var(--font-oranienbaum);
  font-style: normal;
  font-weight: 400;
  font-size: 56px;
  line-height: 95%;
  color: #020202;
}
.argument-content-info p span {
  font-family: var(--font-inter);
  font-style: normal;
  font-weight: 300;
  font-size: 42px;
  line-height: 113%;
  letter-spacing: -0.04em;
}
.argument-content-info p {
  font-family: var(--font-inter);
  font-style: normal;
  font-weight: 300;
  font-size: 16px;
  line-height: 114%;
  letter-spacing: -0.02em;
  color: rgba(2, 2, 2, 0.8);
}
.argument-content-info p:nth-last-child(2) {
  margin: 29px 0 8px;
}
.argument-media-wrapper {
  width: 509px;
  height: 351px;
  border-radius: 24px;
  border: 1px solid #25252c;
}

@keyframes slidein {
  from {
    transform: translateX(200%);
  }

  to {
    transform: translateX(0%);
  }
}
@keyframes slideout {
  from {
    transform: translateX(0%);
  }

  to {
    transform: translateX(-200%);
  }
}
.slide_in {
  animation-name: slidein;
  animation-duration: 2s;
  animation-fill-mode: forwards;
  animation-timing-function: cubic-bezier(0.275, 0.985, 0.52, 1.175);
}
.slide_out {
  animation-name: slideout;
  animation-duration: 2s;
  animation-fill-mode: forwards;
  animation-timing-function: cubic-bezier(0.275, 0.985, 0.52, 1.175);
}

@media screen and (max-width: 767px) {
  .argumentofhouse-container {
    height: auto;
  }
  .argumentofhouse-wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-top: 15px;
  }
  .argument-content-wrapper {
    width: 100%;
    height: 620px;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    overflow: hidden;
  }
  .argument-navbar {
    margin-bottom: 23px;
  }
  .argument-content-item {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    position: absolute;
    width: 90%;
  }
  .argument-content-info {
    width: 90%;
    margin-right: 0;
  }
  .argument-content-info p:first-child {
    font-size: 36px;
  }
  .argument-content-info p span {
    font-size: 22px;
  }
  .argument-content-info p {
    font-size: 14px;
  }
  .argument-content-info p:nth-last-child(2) {
    margin: 22px 0 8px;
  }
  .argument-media-wrapper {
    width: 100%;
    height: 351px;
    border-radius: 24px;
    margin-top: 22px;
  }
}
